import React from "react";
import "./App.scss";
import ChatApp from "./ChatApp";
import Header from "./Header";
import LoginForm from "./LoginForm";

function App() {
  return (
    <div className="App">
      <Header />
      <div className="main">
        {!localStorage.getItem("userName") ? (
          <LoginForm />
        ) : (
          <ChatApp/>
        )}
      </div>
    </div>
  );
}

export default App;
